<template>
    <div class="html">
        <div class="cp">
            <h2>公司证照</h2>
			<img src="@/assets/about/cp.jpg" alt="">
			<!-- <img :src="img" alt=""> -->
	</div>
	</div>
</template>

<script>
import reuqest from "@/utils/request";
export default {
	components:{
    },
	name: 'HelloWorld',
	data () {
		return {
			img :""
		}
	},
	created(){
		reuqest({
			url: '/api/getArticleDetaild',
			method: "post",
			data:{
				type: 'cooperative_brand '
			}
		}).then(data=>{
			console.log(data)
			this.img = data.data.data.image
		})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.html {
    background: #e6e6e6;
}
.cp {
    display: inline-block;
    width: 1200px;
    background: white;
    margin: 0 auto;
    padding: 0 50px 20px;
}
h2 {
    font-size: 20px;
    padding-bottom: 10px;
    color: #333;
    border-bottom: 1px solid #999;
    margin-bottom: 20px;
    margin-top: 18px;
    text-align: left;
}
.pc img{
	width: 1024px;
	height: 653px;
}
</style>



